import { h } from "@stencil/core";
export class RaulContainerHeader {
    render() {
        return [
            h("div", { class: "r-container__header__content" },
                h("slot", null)),
            h("slot", { name: "container-header-actions" })
        ];
    }
    static get is() { return "raul-container-header"; }
    static get originalStyleUrls() { return {
        "$": ["raul-container-header.scss"]
    }; }
    static get styleUrls() { return {
        "$": ["raul-container-header.css"]
    }; }
}
